.card{
    background-color: red;
}

.magic_box{
    border-radius: 1rem;
    padding: 0.4rem 1.5rem ;
    height: fit-content;
    color: white;
    font-size: 1.5rem;
    background: linear-gradient(90deg ,rgb(255, 192, 75), rgb(249, 170, 42));
    box-shadow: 0px 0px 10px rgba(244, 159, 1 , 0.3);
}

.sub_card{
    padding: 1rem 0rem;
    width: 4.5rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.sub_card:first-child{
    margin-right: 1rem;
}